/*  more魔王 */
.moreItem {
  align-items: flex-end; }
  .moreItem span {
    position: absolute;
    color: #AA0601;
    font-size: 4.5rem;
    font-weight: 700;
    transform: translate(0%, 20%);
    white-space: nowrap;
    z-index: 1; }
    @media (max-width: 768px) {
      .moreItem span {
        font-size: 2.5rem;
        transform: translate(0%, -10%); } }
    @media (max-width: 576px) {
      .moreItem span {
        font-size: 26px;
        transform: translate(0%, -15%); } }
    .moreItem span::before {
      content: 'MORE';
      position: absolute;
      transition: all .6s .3s;
      transform: translateY(0); }
    .moreItem span::after {
      content: 'MORE';
      position: absolute;
      transition: all .6s .3s;
      transform: translateY(0); }
    .moreItem span:hover::before {
      transform: translateY(-85px);
      opacity: .7; }
    .moreItem span:hover::after {
      transform: translateY(-145px);
      opacity: .3; }

.moreImg {
  position: relative;
  height: 100%;
  max-width: 30%;
  transition: all .6s .3s;
  transform: translateX(0);
  margin-right: 85px; }
  .moreImg::after {
    content: '';
    position: absolute;
    background: #ffffff;
    top: 15%;
    bottom: 15%;
